import * as React from "react"
import PropTypes from "prop-types"

import DefaultLayout from "./layout_default"
import TeamLayout from "./layout_team"
import NewsLayout from "./layout_news"

import "../../assets/stylesheets/mystyles.scss"

const Layout = ({ children, frontmatter, title }) => {
  if (frontmatter) {
    if (frontmatter.type === 'team') {
      return <TeamLayout frontmatter={frontmatter}>{children}</TeamLayout>
    }
    return <NewsLayout frontmatter={frontmatter}>{children}</NewsLayout>
  }
  return <DefaultLayout title={title}>{children}</DefaultLayout>
}

Layout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default Layout
