import * as React from "react"

const NavItem = ({ item }) => {
  return (
    <>
    {item.children.length === 0 && (
      <a href={item.url} className={item.children.length > 0 ? "navbar-item has-dropdown is-hoverable" : "navbar-item"}>
      {item.title}
      </a>
    )}
    {item.children.length > 0 && (
      <div className="navbar-item has-dropdown is-hoverable">
        <a href={item.url} className="navbar-link">
        {item.title}
        </a>
        <div className="navbar-dropdown is-hoverable">
        {item.children.map((e, index) => {
          return (
            <a key={index} href={e.url} className="navbar-item">
              {e.title}
            </a>
          );
        })}
        </div>
      </div>
    )}
    </>
  )
}
export default NavItem
