import React from 'react'

import Gallery from '@browniebroke/gatsby-image-gallery'
// https://github.com/browniebroke/gatsby-image-gallery

const lightboxOptions = {
    imageLoadErrorMessage: 'Dieses Bild konnte nicht geladen werden',
    nextLabel: 'Nächstes Bild',
    prevLabel: 'Vorheriges Bild',
    zoomInLabel: 'Vergrössern',
    zoomOutLabel: 'Verkleinern',
    closeLabel: 'Schliessen',
}

const TeamGallery = ({ frontmatter }) => {
  // (({ node }, index)
  // const images = frontmatter.images.map(({ url }) => url.childImageSharp)
  const images = frontmatter.images.map(({ caption, name, url }) => ({
    ...url.childImageSharp,
    title: name,
    caption: caption,
    thumbAlt: name
  }))
  return (
    <section className="is-primary">
      <div className="container is-fluid my-4">
        <div className="content">
          <h3>Bilder</h3>
        </div>
        <Gallery images={images}
          lightboxOptions={lightboxOptions} />
      </div>
    </section>
  )
}

export default TeamGallery
