import * as React from "react"
import { useStaticQuery, graphql } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"

const Sponsoren = ({ item }) => {
  const data = useStaticQuery(graphql`
    query sponsoren {
      allMdx(filter: {frontmatter: {slug: {regex: "/sponsoren\/"}}}, sort: {fields: frontmatter___weight}) {
        nodes {
          frontmatter {
            title
            slug
            sponsor {
              type
              url
              image {
                childImageSharp {
                  gatsbyImageData(
                    layout: CONSTRAINED
                    width: 290
                    height: 190
                    placeholder: BLURRED
                    transformOptions: {fit:INSIDE, cropFocus:ATTENTION}
                    formats: [AUTO, WEBP, AVIF]
                  )
                }
              }
            }
          }
        }
      }
    }
  `)

  return (
    <>
    <div className="columns is-mobile is-multiline is-vcentered">
    {data.allMdx.nodes.map((sponsor, index) => {
      return (
        <div key={index} className="column is-6-mobile is-2-tablet is-1-widescreen">
          <GatsbyImage
            image={getImage(sponsor.frontmatter.sponsor.image)}
            alt={sponsor.frontmatter.title}
          />
        </div>
      );
    })}
    </div>
    </>
  )
}
export default Sponsoren
