import * as React from "react"
import PropTypes from "prop-types"

import Header from "../header"
import Hero from "../hero"
import TeamInfo from "../teams/teaminfo"
import Sponsoren from "../sponsoren"
import TeamGallery from "../teams/gallery"
import ResultCombo from "../teams/result_combo"

const TeamLayout = ({ children, frontmatter }) => {
  return (
    <>
    <Header/>
    <Hero title={frontmatter.title}/>
    {(frontmatter.team_info) && (
      <TeamInfo frontmatter={frontmatter}/>
    )}
    {(frontmatter.shv_id) && (
      <ResultCombo shv_id={frontmatter.shv_id}/>
    )}
    {(frontmatter.images && frontmatter.images.length > 0) && (
      <TeamGallery frontmatter={frontmatter}/>
    )}
    {children}
    <footer className="footer has-text-centered">
      <Sponsoren/>
    </footer>
    </>
  )
}

TeamLayout.propTypes = {
  children: PropTypes.node.isRequired,
}

export default TeamLayout
