/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import * as React from "react"

import Trainer from "./trainer"
import Training from "./training"

const TeamInfo = ({ frontmatter }) => {
  return (
    <section className="is-primary">
      <div className="container is-fluid my-4">
      {((frontmatter.team_info.trainer.length >= 0) || (frontmatter.team_info.trainings && frontmatter.team_info.trainings.length >= 0)) && (
        <div className="columns">
          {(frontmatter.team_info.trainer.length >= 0) && (
            <Trainer trainers={frontmatter.team_info.trainer}/>
          )}
          {(frontmatter.team_info.trainings && frontmatter.team_info.trainings.length >= 0) && (
            <>
            {frontmatter.team_info.trainings.map((training, index) => {
              return (
                <Training key={`training-${index}`} training={training} index={index}/>
              );
            })}
            </>
          )}
        </div>
        )}
      </div>
    </section>
  )
}

export default TeamInfo


